@import "ScssHelpers/helpers-all.scss";

.result_list {
	@include bp($point_2, min) {
		padding-top: 2rem;
	}
}

.result_list__items {
	list-style: none;
	padding: 0;
	margin: 0;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.result_list__item {
	&:not(:last-child) {
		border-bottom: 1px solid $beige_2;
	}

	@include bp($point_2, min) {
		padding: 4.2rem 0 6rem;
	}
	@include bp($point_3) {
		padding: 3rem 0 4rem;
	}
}

.result_list__label {
	text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: .8rem;
	}

	@include h5;
}

.result_list__title {
	line-height: 1.05;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.5rem;
		}
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}

	@include h3;
}

.result_list__link {
	color: $black;

	&:hover {
		color: $gold_2;
		text-decoration: none
	}

	@include transition_color;
}

.result_list__row {
	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start);
	}
}

.result_list__text {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-right: 4.4rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 3.6rem;
		}
	}

	@include text;

	@include bp($point_2, min) {
		line-height: 1.5;
	}
	@include bp($point_3) {
		line-height: 1.44;
	}
}

.result_list__btn {
	@include bp($point_6 - 1) {
		width: 100%;
	}
}
